<template>
  <v-container>
    <v-col>
      <v-row>
        <div style="text-align: center">
          <h2 v-if="id" class="mx-auto">პარტნიორის რედაქტირება</h2>
          <h2 v-else class="mx-auto">პარტნიორის დამატება</h2>
          <br />

          <v-row>
            <v-col>
              <v-row>
                <v-card class="mx-auto" style="text-align: left" width="600" height>
                  <v-card-text>
                    <v-row align="center" justify="center">
                      <v-col>
                        <input type="file" @change="handleFileUpload" />
                      </v-col>

                      <v-col v-if="id" cols="12" sm="6">
                        <img :src="logoUrl" style="height:80px; max-width:250px" />
                      </v-col>
                    </v-row>

                    <v-text-field label="სახელი" regular dense v-model="name"></v-text-field>

                    <v-row>
                      <v-col cols="12" sm="6">
                        <v-text-field label="მისამართი" regular dense v-model="address.street"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6">
                        <v-text-field label="ტელ. ნომერი" regular dense v-model="contact.number"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6">
                        <v-text-field label="Email" regular dense v-model="contact.email"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6">
                        <v-text-field label="Website" regular dense v-model="webSite"></v-text-field>
                      </v-col>
                    </v-row>

                    <v-card class="mx-auto" style="text-align:left" :outlined="true" width="450">
                      <v-card-text>
                        <div>საკონტაქტო პირი</div>
                        <v-row>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              label="სახელი"
                              regular
                              dense
                              v-model="contactPerson.firstName"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              label="გვარი"
                              regular
                              dense
                              v-model="contactPerson.lastName"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              label="ტელ. ნომერი"
                              regular
                              dense
                              v-model="contactPerson.number"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field label="Email" regular dense v-model="contactPerson.email"></v-text-field>
                          </v-col>
                        </v-row>
                      </v-card-text>
                    </v-card>

                    <v-checkbox v-model="isActive" label="აქტიური"></v-checkbox>

                    <v-btn color="si_light" dark v-on:click="onClick()">შენახვა</v-btn>
                  </v-card-text>
                </v-card>
              </v-row>
            </v-col>
          </v-row>
        </div>
      </v-row>
    </v-col>
  </v-container>
</template>


<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";

Vue.use(VueAxios, axios, router);

export default {
  name: "PartnersSave",

  data() {
    return {
      name: "",
      address: {
        street: ""
      },
      contact: {
        number: "",
        email: ""
      },
      contactPerson: {
        firstName: "",
        lastName: "",
        number: "",
        email: ""
      },
      webSite: "",
      logoUrl: "",
      file: "",
      id: "",
      isActive: true
    };
  },

  mounted: function() {
    this.id = this.$route.query.id;
    var self = this;
    axios.get("/api/Partners/Get/" + this.id).then(response => {
      self.name = response.data.name;
      self.address = response.data.address;
      self.logoUrl = response.data.logo.url;
      self.isActive = response.data.isActive;
      self.contact = response.data.contactInfo;
      self.contactPerson = response.data.contactPerson;
      self.webSite = response.data.webSite;
    });
  },

  methods: {
    handleFileUpload: function(e) {
      this.file = e.target.files[0];
    },

    imageUpload: function(fn) {
      const mypostparameters = new FormData();
      mypostparameters.append("file", this.file, this.file.name);
      var self = this;

      axios
        .post("/media/images", mypostparameters)
        .then(response => {
          if (!response.data.isSuccess) {
            alert(response.data.message);
          } else {
            self.logoUrl = response.data.data;
            fn();
          }
        })
        .catch(function() {
          console.log("Failure!");
        });
    },

    partnerUpload: function() {
      var self = this;
      axios
        .post("/api/Partners/SaveNew", {
          name: self.name,
          address: self.address,
          logoUrl: self.logoUrl,
          contact: self.contact,
          contactPerson: self.contactPerson,
          webSite: self.webSite,
          isActive: self.isActive
        })
        .then(response => {
          if (!response.data.isSuccess) {
            alert(response.data.message);
          } else {
            self.$router.push({ path: "/partners" });
          }
        });
    },

    partnerEdit: function() {
      var self = this;
      axios
        .put("/api/Partners/Update", {
          id: self.id,
          name: self.name,
          address: self.address,
          logoUrl: self.logoUrl,
          isActive: self.isActive,
          contactPerson: self.contactPerson,
          contact: self.contact,
          webSite: self.webSite
        })
        .then(response => {
          if (!response.data.isSuccess) {
            alert(response.data.message);
          } else {
            self.$router.push({ path: "/partners" });
          }
        });
    },

    onClick: function() {
      if (this.id) {
        if (this.name) {
          if (this.file) {
            this.imageUpload(this.partnerEdit);
          } else {
            this.partnerEdit();
          }
        } else {
          alert("სახელი mandatory ველია");
        }
      } else {
        if (this.name) {
          if (this.file) {
            this.imageUpload(this.partnerUpload);
          } else {
            this.partnerUpload();
          }
        } else {
          alert("სახელი აუცილებელი ველია");
        }
      }
    }
  }
};
</script>