<template>
  <v-container>
    <v-col>
      <v-row>
        <div style="text-align: center">
          <v-row>
            <div v-for="(item, index) in missions" v-bind:key="index">
              <v-btn
                v-if="item.id==currentMission.id"
                class="ma-2"
                fab
                dark
                color="si_light"
              >{{missions[index].level}}</v-btn>
              <v-btn
                v-else
                class="ma-2"
                fab
                outlined
                color="si_light"
                dark
                v-on:click="onMissionChange(item)"
              >{{missions[index].level}}</v-btn>
            </div>
          </v-row>

          <v-row>
            <v-col>
              <v-row>
                <v-card class="mx-auto" style="text-align: left" width="600" height>
                  <v-card-text>
                    <v-row>
                      <v-col>
                        <v-text-field label="სახელი" regular dense v-model="currentMission.name"></v-text-field>
                      </v-col>
                      <v-col cols="4">
                        <v-text-field
                          label="მისიის დრო საათებში"
                          regular
                          dense
                          type="number"
                          v-model="currentMission.durationInHours"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-col>
                      <v-card class="mx-auto" style="text-align:left" :outlined="true" width="450">
                        <v-card-text>
                          <div>ფასი</div>
                          <v-row>
                            <v-col cols="12" sm="6">
                              <v-text-field
                                label="-დან"
                                regular
                                dense
                                type="number"
                                v-model="currentMission.priceRange.from"
                              ></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6">
                              <v-text-field
                                label="-მდე"
                                regular
                                dense
                                type="number"
                                v-model="currentMission.priceRange.to"
                              ></v-text-field>
                            </v-col>
                          </v-row>
                        </v-card-text>
                      </v-card>
                    </v-col>
                    <v-col>
                      <v-textarea
                        label="აღწერა"
                        regular
                        dense
                        rows="4"
                        v-model="currentMission.description"
                      ></v-textarea>
                    </v-col>

                    <v-col>
                      <v-btn color="si_light" dark v-on:click="onClick()">შენახვა</v-btn>
                    </v-col>
                  </v-card-text>
                </v-card>
              </v-row>
            </v-col>
          </v-row>
        </div>
      </v-row>
    </v-col>
  </v-container>
</template>


<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";

Vue.use(VueAxios, axios, router);

export default {
  name: "MissionsSave",

  data() {
    return {
      currentMission: {
        name: "",
        description: "",
        priceRange: {
          from: 0,
          to: 0
        },
        level: 0,
        durationInHours: 0,
        id: ""
      },
      missions: []
    };
  },

  mounted: function() {
    var self = this;
    this.currentMission.id = this.$route.query.id;

    axios.get("/api/Missions/All/").then(response => {
      self.missions = response.data;

      if (self.currentMission.id) {
        self.currentMission = {
          ...self.missions.find(param => param.id == self.currentMission.id)
        };
      } else {
        self.currentMission = { ...self.missions[0] };
      }
    });
  },

  methods: {
    onMissionChange(item) {
      this.$router.push({ path: "/missions", query: { id: item.id } });
      this.currentMission = { ...item };
    },

    onClick: function() {
      var self = this;
      axios
        .put("/api/Missions/Update", {
          id: self.currentMission.id,
          name: self.currentMission.name,
          description: self.currentMission.description,
          durationInHours: self.currentMission.durationInHours,
          priceFrom: self.currentMission.priceRange.from,
          priceTo: self.currentMission.priceRange.to
        })
        .then(response => {
          alert(response.data.message);
        });
    }
  }
};
</script>