<template>
  <v-container>
    <v-row>
      <v-col>
        <v-btn class="mx-2" fab dark color="si_light" v-on:click="onAddNew()">
          <v-icon dark>mdi-plus</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="4">
        <v-text-field label="ძებნა" v-model="searchWord" append-icon="mdi-magnify"></v-text-field>
      </v-col>
    </v-row>

    <v-data-table
      :headers="headers"
      :items="categories"
      :items-per-page="10"
      :search="searchWord"
      class="elevation-1"
    >
      <template v-slot:item.isActive="{ item }">
        <v-checkbox v-model="item.isActive" disabled></v-checkbox>
      </template>
      <template v-slot:item.iconUrl="{ item }">
        <v-img :src="item.iconUrl" aspect-ratio="1.7"></v-img>
      </template>
      <template v-slot:item.id="{ item }">
        <v-btn class="mx-2" fab dark small color="si_light" v-on:click="categoryEdit(item.id)">
          <v-icon dark>mdi-pencil</v-icon>
        </v-btn>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";

Vue.use(VueAxios, axios, router);

export default {
  name: "CategoriesListComponent",

  data() {
    return {
      searchWord: "",
      headers: [
        {
          text: "სტატუსი",
          value: "isActive",
          width: "105px"
        },
        {
          value: "iconUrl",
          width: "100px",
          sortable: false
        },
        {
          text: "კატეგორია",
          align: "start",
          value: "name"
        },
        {
          value: "id",
          width: "40px",
          sortable: false
        }
      ],
      categories: []
    };
  },

  methods: {
    onAddNew: function() {
      this.$router.push({ path: "/categories/add" });
    },
    categoryEdit: function(id) {
      this.$router.push({ path: "/categories/edit", query: { id: id } });
    }
  },

  mounted: function() {
    var self = this;
    axios.get("api/Categories/All").then(response => {
      self.categories = response.data;
    });
  }
};
</script>