<template>
  <v-container>
    <v-row>
      <v-col>
        <v-btn class="mx-2" fab dark color="si_light" v-on:click="onAddNew()">
          <v-icon dark>mdi-plus</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="4">
        <v-text-field label="ძებნა" v-model="searchWord" append-icon="mdi-magnify"></v-text-field>
      </v-col>
    </v-row>
    <v-data-table
      :headers="headers"
      :items="partners"
      :items-per-page="10"
      :options.sync="pagination"
      :server-items-length="itemsQuantity"
      :loading="loading"
      class="elevation-1"
    >
      <template v-slot:item.isActive="{ item }">
        <v-checkbox v-model="item.isActive" disabled></v-checkbox>
      </template>
      <template v-slot:item.logo="{ item }">
        <v-img :src="item.logo.url" aspect-ratio="1.7"></v-img>
      </template>
      <template
        v-slot:item.contactPerson="{ item }"
      >{{item.contactPerson.firstName + ' ' + item.contactPerson.lastName}}</template>

      <template v-slot:item.id="{ item }">
        <v-btn class="mx-2" fab dark small color="si_light" v-on:click="partnerEdit(item.id)">
          <v-icon dark>mdi-pencil</v-icon>
        </v-btn>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";

Vue.use(VueAxios, axios, router);

export default {
  name: "PartnersListComponent",

  data() {
    return {
      searchWord: "",
      itemsQuantity: 0,
      loading: true,
      pagination: {},
      headers: [
        {
          text: "სტატუსი",
          value: "isActive",
          width: "20px",
          sortable: false
        },
        {
          value: "logo",
          width: "100px",
          sortable: false
        },
        {
          text: "დასახელება",
          align: "start",
          value: "name",
          sortable: false
        },
        {
          value: "contactPerson",
          text: "საკონტაქტო პირი",
          sortable: false
        },
        {
          value: "contactPerson.number",
          text: "საკონტაქტო პირის ნომერი",
          sortable: false
        },
        {
          value: "id",
          width: "40px",
          sortable: false
        }
      ],
      partners: []
    };
  },

  methods: {
    onAddNew: function() {
      this.$router.push({ path: "/partners/add" });
    },
    partnerEdit: function(id) {
      this.$router.push({ path: "/partners/edit", query: { id: id } });
    },
    getData: function() {
      var self = this;
      self.loading = true;
      axios
        .get("/api/Partners/Range", {
          params: {
            take: self.pagination.itemsPerPage,
            skip:
              self.pagination.page * self.pagination.itemsPerPage -
              self.pagination.itemsPerPage,
            searchWord: self.searchWord
          }
        })
        .then(response => {
          self.partners = response.data.items;
          self.itemsQuantity = response.data.quantity;
          self.loading = false;
        });
    }
  },

  mounted: function() {
    this.getData();
  },

  computed: {
    params() {
      return {
        ...this.pagination,
        query: this.searchWord
      };
    }
  },

  watch: {
    params: {
      handler() {
        this.getData();
      },
      deep: true
    }
  }
};
</script>