<template>
    <v-container>
      <v-col >
        <v-row
        >
          <div
            style="text-align: center"
          >
      <h2 class='mx-auto'>
        მიმდინარე ბონუსი: {{bonus}}₾
      </h2>
      <br>
      <v-card  
      class="mx-auto"
      style="text-align: left"
        width="400"
        height="">
        <v-card-text> 
          <div> ბონუსის ცვლილების ფორმა
          </div>
        <br>

        <v-text-field
            label="ბონუსი"
            regular
            dense
            type="number"
            v-model.number="pendingBonus"
          ></v-text-field>
        
          <v-btn color="si_light" 
          dark
          v-on:click="onBonusSave()"
          >შენახვა</v-btn>
        </v-card-text>
        
      </v-card>
      </div>
        </v-row>
      </v-col>
    </v-container>
    
</template>


<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios)

  export default {
    name: 'Bonus',

    mounted : function() {
      var self = this
          axios
            .get('/api/Bonus/GetPending')
            .then(response => {self.bonus = response.data.currentAmount})
    },

    data() {
      return {
        bonus: 0,
        pendingBonus: null
      }
    },

    methods: {
      onBonusSave: function(){
        if (this.pendingBonus != null) {
          this.bonus=this.pendingBonus
          this.pendingBonus = null
          var self = this
          axios
            .post('/api/Bonus/Save',
            {
                amount:self.bonus
            })
            .then(response => {
              if (!response.data.isSuccess) {
                alert(response.data.message)
              }

            })

        }
      }

    }
        
    } 
</script>