import HelloWorld from './components/HelloWorld'
import TestComponent from './components/TestComponent'
import BonusComponent from './views/bonus/BonusComponent'
import CategoriesListComponent from './views/categories/CategoriesListComponent'
import CategoriesSaveNewComponent from './views/categories/CategoriesSaveNewComponent'
import PartnersListComponent from './views/partners/PartnersListComponent'
import PartnersSaveComponent from './views/partners/PartnersSaveComponent'
import ProductsListComponent from './views/products/ProductsListComponent'
import ProductsSaveComponent from './views/products/ProductsSaveComponent'
import MissionsSaveComponent from './views/missions/MissionsSaveComponent'
import AboutUsComponent from './views/content/AboutUsComponent'
import GameRulesComponent from './views/content/GameRulesComponent'
import ContactUsComponent from './views/content/ContactUsComponent'




export default [
  { name: 'home', path: '/', component: HelloWorld },
  { name: 'TestComponent.view', path: '/test', component: TestComponent },
  { name: 'BonusComponent.view', path: '/bonus', component: BonusComponent},
  { name: 'CategoriesListComponent.view', path: '/categories', component: CategoriesListComponent},
  { name: 'CategoriesSaveNewComponent.view', path: '/categories/add', component: CategoriesSaveNewComponent},
  { name: 'CategoriesEditComponent.view', path: '/categories/edit', component: CategoriesSaveNewComponent},
  { name: 'PartnersListComponent.view', path: '/partners', component: PartnersListComponent},
  { name: 'PartnersSaveComponent.view', path: '/partners/add', component: PartnersSaveComponent},
  { name: 'PartnersEditComponent.view', path: '/partners/edit', component: PartnersSaveComponent},
  { name: 'ProductsListComponent.view', path: '/products', component: ProductsListComponent},
  { name: 'ProductsSaveComponent.view', path: '/products/add', component: ProductsSaveComponent},
  { name: 'ProductsEditComponent.view', path: '/products/edit', component: ProductsSaveComponent},
  { name: 'MissionsSaveComponent.view', path: '/missions', component: MissionsSaveComponent},
  { name: 'AboutUs.view', path: '/content/about-us', component: AboutUsComponent},
  { name: 'GameRules.view', path: '/content/game-rules', component: GameRulesComponent},
  { name: 'ContactUs.view', path: '/content/contact-us', component: ContactUsComponent},
]