<template>
  <v-container>
    <ckeditor :editor="editor" v-model="editorData" :config="editorConfig"></ckeditor>
    <br>
    <v-btn color="si_light" dark v-on:click="onClick()">შენახვა</v-btn>
  </v-container>
</template>
<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";
import CKEditor from "@ckeditor/ckeditor5-vue";
// import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
// import SimpleUploadAdapter from "@ckeditor/ckeditor5-upload"

Vue.use(VueAxios, axios, router, CKEditor);

export default {
  name: "GameRules",

  data: () => ({
    name: "gamerules",
    // editor: ClassicEditor,
    editorData: "<p>Content of the editor.</p>",
    editorConfig: {
    //   plugins : [SimpleUploadAdapter],
    //   simpleUpload : {
    //       uploadUrl : '/media/images'
    //   }
    }
  }),
  components: {
    // Use the <ckeditor> component in this view.
    ckeditor: CKEditor.component
  },

  mounted: function() {
    var self = this;
    axios.get("/api/Content/" + this.name).then(response => {
      self.editorData = response.data.value;
    });
  },
  methods: {
    onClick() {
      var self = this;
      axios.post("/api/Content",{
          name : self.name,
          value : self.editorData
      }).then(response => alert(response.data.message));
    }
  }
};
</script>