<template>
  <v-container>
    <v-col>
      <v-row>
        <div style="text-align: center">
          <h2 v-if="id" class="mx-auto">კატეგორიის რედაქტირება</h2>
          <h2 v-else class="mx-auto">კატეგორიის დამატება</h2>
          <br />
          <v-row>
            <v-col v-if="id">
              <v-row>
                <v-img :src="iconUrl" aspect-ratio="1.7" height="200px" width="200px"></v-img>
              </v-row>
            </v-col>
            <v-col>
              <v-row>
                <v-card class="mx-auto" style="text-align: left" width="400" height>
                  <v-card-text>
                    <input type="file" @change="handleFileUpload" />
                    <br />
                    <br />

                    <v-text-field label="სახელი" regular dense v-model="name"></v-text-field>

                    <v-text-field label="ფერი" regular dense v-model="color"></v-text-field>
                    <v-checkbox v-model="isActive" label="აქტიური"></v-checkbox>

                    <v-btn color="si_light" dark v-on:click="onClick()">შენახვა</v-btn>
                  </v-card-text>
                </v-card>
              </v-row>
            </v-col>
          </v-row>
        </div>
      </v-row>
    </v-col>
  </v-container>
</template>


<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";

Vue.use(VueAxios, axios, router);

export default {
  name: "CategoriesSaveNew",

  data() {
    return {
      name: "",
      color: "",
      iconUrl: "",
      file: "",
      id: "",
      isActive: true
    };
  },

  mounted: function() {
    this.id = this.$route.query.id;
    var self = this;
    axios.get("/api/Categories/Get/" + this.id).then(response => {
      self.name = response.data.name;
      self.color = response.data.color;
      self.iconUrl = response.data.iconUrl;
      self.isActive = response.data.isActive;
    });
  },

  methods: {
    handleFileUpload: function(e) {
      this.file = e.target.files[0];
    },

    imageUpload: function(fn) {
      const mypostparameters = new FormData();
      mypostparameters.append("file", this.file, this.file.name);
      var self = this;

      axios
        .post("/media/images", mypostparameters)
        .then(response => {
          if (!response.data.isSuccess) {
            alert(response.data.message);
          } else {
            self.iconUrl = response.data.data;
            fn();
          }
        })
        .catch(function() {
          console.log("Failure!");
        });
    },

    categoryUpload: function() {
      var self = this;
      axios
        .post("/api/Categories/SaveNew", {
          name: self.name,
          color: self.color,
          iconUrl: self.iconUrl,
          isActive: self.isActive
        })
        .then(response => {
          if (!response.data.isSuccess) {
            alert(response.data.message);
          } else {
            self.$router.push({ path: "/categories" });
          }
        });
    },

    categoryEdit: function() {
      var self = this;
      axios
        .put("/api/Categories/Update", {
          id: self.id,
          name: self.name,
          color: self.color,
          iconUrl: self.iconUrl,
          isActive: self.isActive
        })
        .then(response => {
          if (!response.data.isSuccess) {
            alert(response.data.message);
          } else {
            self.$router.push({ path: "/categories" });
          }
        });
    },

    onClick: function() {
      if (this.id) {
        if (this.name && this.color) {
          if (this.file) {
            this.imageUpload(this.categoryEdit);
          } else {
            this.categoryEdit();
          }
        } else {
          alert("სახელი და ფერი mandatory ველებია");
        }
      } else {
        if (this.name && this.file && this.color) {
          this.imageUpload(this.categoryUpload);
        } else {
          alert("სახელი, ფერი და icon აუცილებელი ველებია");
        }
      }
    }
  }
};
</script>