<template>

      <v-navigation-drawer
        app
        floating
        v-model="drawer"
        :color="color"
        :expand-on-hover="expandOnHover"
        :mini-variant="miniVariant"
        :right="right"
        :permanent="permanent"
        :src="bg"
        dark
      >
        <v-list
          dark
          dense
        >
          <v-list-item style="min-height: 55px;" :class="miniVariant && 'px-0'">
            <div>
              <img style="height: 40px" src="http://test.si.ge/img/logo-white.svg">
            </div>

          </v-list-item>

          <v-divider></v-divider>

          <router-link :to='item.link'
          v-for="item in items"
            :key="item.title">
          <v-list-item
            link
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>

          </v-list-item>
           </router-link>
        </v-list>
      </v-navigation-drawer>

</template>


<script>
  export default {
    name: 'Navigation',

    data () {
      return {
        drawer: true,
        items: [
          { title: 'ბონუსი', icon: 'mdi-cash-usd', link: '/bonus' },
          { title: 'კატეგორიები', icon: 'mdi-format-list-bulleted', link: '/categories'},
          { title: 'პარტნიორები', icon: 'mdi-store', link: '/partners'},
          { title: 'პროდუქტები', icon: 'mdi-store', link: '/products'},
          { title: 'მისიები', icon: 'mdi-flag-triangle', link: '/missions'},
          { title: 'ჩვენ შესახებ', icon: 'mdi-account-multiple', link: '/content/about-us'},
          { title: 'თამაშის წესები', icon: 'mdi-clipboard-text', link: '/content/game-rules'},
          { title: 'კონტაქტი', icon: 'mdi-phone', link: '/content/contact-us'},
        ],
        color: 'si_light',
        colors: [
          'primary',
          'blue',
          'success',
          'red',
          'teal',
        ],
        right: false,
        permanent: true,
        miniVariant: false,
        expandOnHover: false,
        background: false,
      }
    },
    computed: {
      bg () {
        return this.background ? 'https://cdn.vuetifyjs.com/images/backgrounds/bg-2.jpg' : undefined
      },
    },
  }
</script>