          <v-row class="fill-height" >
             <v-col>
               <router-view></router-view>
             </v-col>
          </v-row>
      </v-container>
    </v-content>



    <v-footer>
    </v-footer>
  </v-app>
</template>

<script>
import Navigation from './components/general/Navigation';
import Login from './views/login/LogIn';

export default {
  name: 'App',

  components: {
    Navigation,
    Login
  },

  mounted: function() {
      this.isSignedIn = localStorage.getItem('isSignedIn')
  },

  data: () => ({
    isSignedIn : 0
  }),
};
</script>

