<template>
  <v-container>
    <v-row>
      <v-col>
        <v-btn class="mx-2" fab dark color="si_light" v-on:click="onAddNew()">
          <v-icon dark>mdi-plus</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="4">
        <v-text-field label="ძებნა" v-model="searchWord" append-icon="mdi-magnify"></v-text-field>
      </v-col>
    </v-row>
    <v-data-table
      :headers="headers"
      :items="products"
      :items-per-page="10"
      :options.sync="pagination"
      :server-items-length="itemsQuantity"
      :loading="loading"
      class="elevation-1"
    >
      <template v-slot:item.isActive="{ item }">
        <v-checkbox v-model="item.isActive" disabled></v-checkbox>
      </template>
      <template v-slot:item.images="{ item }">
        <v-img :src="item.images[0]? item.images[0].url : ''" aspect-ratio="1.7"></v-img>
      </template>
      <template v-slot:item.connectedProduct="{ item }">
        {{item.connectedProduct.length}}
      </template>
      <!-- <template
        v-slot:item.contactPerson="{ item }"
      >{{item.contactPerson.firstName + ' ' + item.contactPerson.lastName}}</template> -->

      <template v-slot:item.id="{ item }">
        <v-btn class="mx-2" fab dark small color="si_light" v-on:click="productEdit(item.id)">
          <v-icon dark>mdi-pencil</v-icon>
        </v-btn>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";

Vue.use(VueAxios, axios, router);

export default {
  name: "ProductsListComponent",

  data() {
    return {
      searchWord: "",
      itemsQuantity: 0,
      loading: true,
      pagination: {},
      headers: [
        {
          text: "სტატუსი",
          value: "isActive",
          width: "20px",
          sortable: false
        },
        {
          value: "images",
          width: "100px",
          sortable: false
        },
        {
          text: "დასახელება",
          align: "start",
          value: "name",
          sortable: false
        },
        {
          text: "ფასი",
          align: "start",
          value: "price.amount",
          sortable: false
        },
         {
          text: "მონეტა",
          align: "start",
          value: "coin",
          sortable: false
        },
          {
          text: "დაკავშირებული პროექტები",
          align: "start",
          value: "connectedProduct",
          sortable: false
        },
        {
          value: "id",
          width: "40px",
          sortable: false
        }
      ],
      products: []
    };
  },

  methods: {
    onAddNew: function() {
      this.$router.push({ path: "/products/add" });
    },
    productEdit: function(id) {
      this.$router.push({ path: "/products/edit", query: { id: id } });
    },
    getData: function() {
      var self = this;
      self.loading = true;
      axios
        .get("/api/products/Range", {
          params: {
            take: self.pagination.itemsPerPage,
            skip:
              self.pagination.page * self.pagination.itemsPerPage -
              self.pagination.itemsPerPage,
            searchWord: self.searchWord
          }
        })
        .then(response => {
          self.products = response.data.items;
          self.itemsQuantity = response.data.quantity;
          self.loading = false;
        });
    }
  },

  mounted: function() {
   // this.getData();
  },

  computed: {
    params() {
      return {
        ...this.pagination,
        query: this.searchWord
      };
    }
  },

  watch: {
    params: {
      handler() {
        this.getData();
      },
      deep: true
    }
  }
};
</script>