<template>
  <v-container>
    <ckeditor :editor="editor" v-model="editorData"  @ready="onReady" :config="editorConfig"></ckeditor>
    <br>
    <v-btn color="si_light" dark v-on:click="onClick()">შენახვა</v-btn>
  </v-container>
</template>
<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";
import CKEditor from '@ckeditor/ckeditor5-vue';
import ClassicEditor from '@ckeditor/ckeditor5-editor-classic/src/classiceditor';
// import SimpleUploadAdapter from "@ckeditor/ckeditor5-upload"

Vue.use(VueAxios, CKEditor, axios, router);

export default {
  name: "ContactUs",

  data: () => ({
    name: "contactus",
    editor: ClassicEditor,
    editorData: "<p>Content of the editor.</p>",
    editorConfig: {
    //   plugins : [SimpleUploadAdapter],
    //   simpleUpload : {
    //       uploadUrl : '/media/images'
    //   }
    }
  }),
  components: {
    // Use the <ckeditor> component in this view.
    ckeditor: CKEditor.component
  },

  mounted: function() {
    var self = this;
    axios.get("/api/Content/" + this.name).then(response => {
      self.editorData = response.data.value;
    });
  },
  methods: {
    onReady( editor )  {
                // Insert the toolbar before the editable area.
                editor.ui.getEditableElement().parentElement.insertBefore(
                    editor.ui.view.toolbar.element,
                    editor.ui.getEditableElement()
                );
            },
    onClick() {
      var self = this;
      axios.post("/api/Content",{
          name : self.name,
          value : self.editorData
      }).then(response => alert(response.data.message));
    }
  }
};
</script>