<template>
  <v-container>
    <v-col>
      <v-row>
        <div style="text-align: center">
          <!-- <h2 v-if="product.id" class="mx-auto">პროდუქტის რედაქტირება</h2>
          <h2 v-else class="mx-auto">პროდუქტის დამატება</h2>
          <br />-->

          <v-row>
            <v-col>
              <v-row>
                <v-card class="mx-auto" style="text-align: left" width="100%" height>
                  <v-card-text>
                    <v-row align="center" justify="center">
                      <!-- <v-col v-if="product.id" cols="12" sm="6"></v-col> -->

                      <v-col cols="12" sm="6">
                        <v-row>
                          <v-col cols="12" sm="10">
                            <v-text-field label="სახელი" regular dense v-model="product.name"></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-checkbox v-model="product.isActive" label="აქტიური"></v-checkbox>
                          </v-col>
                          <v-col cols="12" sm="12">
                            <v-text-field label="საჩუქარი" regular dense v-model="product.gift"></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="12">
                            <v-textarea
                              label="აღწერა"
                              regular
                              dense
                              rows="3"
                              v-model="product.description"
                            ></v-textarea>
                          </v-col>

                          <v-col cols="12" sm="12">
                            <v-data-table
                              :headers="connectedHeaders"
                              :items="connectedProducts"
                              :items-per-page="5"
                              class="elevation-1"
                            >
                              <template v-slot:top>
                                <v-toolbar flat color="white">
                                  <v-toolbar-title>დაკავშირებული პროდუქტები</v-toolbar-title>
                                  <v-divider class="mx-4" inset vertical></v-divider>
                                  <v-spacer></v-spacer>
                                  <v-dialog v-model="dialog" max-width="500px">
                                    <template v-slot:activator="{ on, attrs }">
                                      <v-btn
                                        class="mx-2"
                                        fab
                                        dark
                                        color="si_light"
                                        v-bind="attrs"
                                        v-on="on"
                                      >
                                        <v-icon dark>mdi-pencil</v-icon>
                                      </v-btn>
                                    </template>
                                    <v-card>
                                      <v-card-title>
                                        <span class="headline">დასაკავშირებლად აირჩიეთ პროდუტი</span>
                                      </v-card-title>

                                      <v-card-text>
                                        <v-container>
                                          <v-autocomplete
                                            v-model="connectedProduct"
                                            :items="connectionItems"
                                            :loading="isLoadingConnections"
                                            :search-input.sync="searchConnection"
                                            item-text="displayName"
                                            item-value="API"
                                            label="პროდუქტი"
                                            return-object
                                          ></v-autocomplete>
                                        </v-container>
                                      </v-card-text>

                                      <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="close">გაუქმება</v-btn>
                                        <v-btn color="blue darken-1" text @click="save">შენახვა</v-btn>
                                      </v-card-actions>
                                    </v-card>
                                  </v-dialog>
                                </v-toolbar>
                              </template>
                            </v-data-table>
                          </v-col>
                        </v-row>
                      </v-col>
                      <v-col cols="12" sm="6" style="text-align: center">
                        <v-row>
                          <v-col cols="12" sm="12">
                            <img
                              :src="product.images[0].url"
                              style="margin: 20px; max-height: 300px"
                            />
                            <br />
                            <input type="file" @change="handleFileUpload" />
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field label="ფასი" regular dense v-model.number="product.price"></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              label="მონეტები"
                              regular
                              dense
                              v-model.number="product.point"
                            ></v-text-field>
                          </v-col>

                          <v-col cols="12" sm="12">
                            <v-autocomplete
                              v-model="product.partner"
                              :items="items"
                              :loading="isLoading"
                              :search-input.sync="searchPartner"
                              item-text="name"
                              item-value="API"
                              label="პარტნიორი"
                              return-object
                            ></v-autocomplete>
                          </v-col>
                          <v-col cols="12" sm="12">
                            <v-autocomplete
                              v-model="product.category"
                              :items="categoryItems"
                              :loading="isLoadingCategory"
                              :search-input.sync="searchCategory"
                              item-text="name"
                              item-value="API"
                              label="კატეგორია"
                              return-object
                            ></v-autocomplete>
                          </v-col>
                        </v-row>
                      </v-col>
                    </v-row>

                    <v-btn color="si_light" dark v-on:click="onClick()">შენახვა</v-btn>
                  </v-card-text>
                </v-card>
              </v-row>
            </v-col>
          </v-row>
        </div>
      </v-row>
    </v-col>
  </v-container>
</template>


<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "vue-router";

Vue.use(VueAxios, axios, router);

export default {
  name: "ProductsSave",

  data() {
    return {
      dialog: false,
      descriptionLimit: 60,
      searchPartner: "",
      searchCategory: "",
      searchConnection: "",
      partners: [],
      categories: [],
      conProducts: [],
      isLoading: false,
      isLoadingCategory: false,
      isLoadingConnections: false,
      connectedProduct: {},
      partner: {},
      category: {},
      product: {
        id: null,
        groupID: null,
        name: null,
        description: null,
        partner: {
          id: null
        },
        price: 0,
        point: 0,
        isActive: true,
        gift: null,
        category: {
          id: null
        },
        images: [
          {
            url: null
          }
        ]
      },
      connectedProducts: [],
      connectedHeaders: [
        {
          text: "დასახელება",
          align: "start",
          value: "name",
          sortable: false
        },
        {
          text: "ფასი",
          align: "start",
          value: "price.amount",
          sortable: false
        },
        {
          text: "მონეტა",
          align: "start",
          value: "coin",
          sortable: false
        }
      ]
    };
  },

  mounted: function() {
    this.product.id = this.$route.query.id;
    if (!this.product.id) return;
    var self = this;
    axios.get("/api/Products/Get/" + this.product.id).then(response => {
      self.product = {
        ...response.data,
        price: response.data.price.amount,
        point: response.data.coin,
        groupID: response.data.productGroupID,
        images: [
          response.data.images[0] ? response.data.images[0] : { url: "" }
        ]
      };

      axios
        .get("/api/Products/Group/" + self.product.groupID)
        .then(resp => (self.connectedProducts = resp.data));
    });
  },

  methods: {
    handleFileUpload: function(e) {
      this.file = e.target.files[0];
    },

    imageUpload: function(fn) {
      const mypostparameters = new FormData();
      mypostparameters.append("file", this.file, this.file.name);
      var self = this;

      axios
        .post("/media/images", mypostparameters)
        .then(response => {
          if (!response.data.isSuccess) {
            alert(response.data.message);
          } else {
            self.product.images[0].url = response.data.data;
            fn();
          }
        })
        .catch(function() {
          console.log("Failure!");
        });
    },

    productUpload: function() {
      var self = this;
      axios.post("/api/Products/SaveNew", self.product).then(response => {
        if (!response.data.isSuccess) {
          alert(response.data.message);
        } else {
          self.$router.push({ path: "/products" });
        }
      });
    },

    productEdit: function() {
      var self = this;
      axios.put("/api/Products/Update", self.product).then(response => {
        if (!response.data.isSuccess) {
          alert(response.data.message);
        } else {
          self.$router.push({ path: "/products" });
        }
      });
    },

    onClick: function() {
      if (this.product.id) {
        if (this.product.name) {
          if (this.file) {
            this.imageUpload(this.productEdit);
          } else {
            this.productEdit();
          }
        } else {
          alert("სახელი mandatory ველია");
        }
      } else {
        if (this.product.name) {
          if (this.file) {
            this.imageUpload(this.productUpload);
          } else {
            this.productUpload();
          }
        } else {
          alert("სახელი აუცილებელი ველია");
        }
      }
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.desserts.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.desserts.splice(index, 1);
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      var self = this;
      if (!this.connectedProduct.productGroupID) {
        delete this.product.groupID;
         self.connectedProducts = [];
        self.close();
      } else {
        this.product.groupID = this.connectedProduct.productGroupID;
        axios.get("/api/Products/Group/" + self.product.groupID).then(resp => {
          self.connectedProducts = resp.data;
          self.close();
        });
      }
    }
  },
  computed: {
    items() {
      var res = this.partners.map(entry => {
        const name =
          entry.name.length > this.descriptionLimit
            ? entry.name.slice(0, this.descriptionLimit) + "..."
            : entry.name;

        return Object.assign({}, entry, { name });
      });
      console.log(res);
      return res;
    },
    connectionItems() {
      var res = this.conProducts.map(entry => {
        return Object.assign({}, entry, {
          displayName: entry.partner.name + ":  " + entry.name
        });
      });
      console.log(res);
      return res;
    },
    categoryItems() {
      var res = this.categories.map(entry => {
        const name =
          entry.name.length > this.descriptionLimit
            ? entry.name.slice(0, this.descriptionLimit) + "..."
            : entry.name;

        return Object.assign({}, entry, { name });
      });
      console.log(res);
      return res;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    searchPartner(val) {
      var self = this;
      this.isLoading = true;
      axios
        .get("/api/Partners/Range?SearchWord=" + (val ? val : ""))
        .then(res => {
          self.partners = res.data.items;
          self.isLoading = false;
        })
        .catch(function() {
          console.log("Failure!");
        });
    },
    searchConnection(val) {
      var self = this;
      this.isLoadingConnections = true;
      axios
        .get("/api/Products/Range?SearchWord=" + (val ? val : ""))
        .then(res => {
          self.conProducts = res.data.items;
          self.isLoadingConnections = false;
        })
        .catch(function() {
          console.log("Failure!");
        });
    },
    searchCategory() {
      if (this.categories.length > 0 || this.isLoadingCategory) return;
      var self = this;
      this.isLoadingCategory = true;

      axios
        .get("/api/categories/all")
        .then(res => {
          self.categories = res.data;
          //  console.log(self.partners);
          self.isLoadingCategory = false;
        })
        .catch(function() {
          console.log("Failure!");
        });
    }
  }
};
</script>